.colapse__block{
    position: relative;
    height: 100%;
    width: 100%;
    .colapse__block__img{
        img{
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;
            border-radius: 25px;
            margin: auto;
            object-fit: cover;
            opacity: 0;
            transform: translateX(-5%);
            transition-duration: 0.3s;
        }
        .visible{
            opacity: 1;
            transform: translateX(0%);
        }
    }
    .colapse__btn__next{
        position: absolute;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        right: 1%;
        transform: rotate(180deg);
        cursor: pointer;
        @media(max-width: 720px){
            right: 3%;
            img{
                width: 1.5rem;
            }
        }
    }
    .colapse__btn__prev{
        position: absolute;
        position: absolute;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        left: 1%;
        cursor: pointer;
        @media(max-width: 720px){
            left: 3%;
            img{
                width: 1.5rem;
            }
        }
    }
    .colapse__info__index{
        position: absolute;
        color: #FFF;
        text-align: center;
        font-family: Montserrat;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 142.6%;
        width: 100%;
        bottom: 5%;
    }
}